import {
  Box,
  Fab,
  IconButton,
  InputAdornment,
  Stack,
  TextField,
  Tooltip,
  Typography,Button
} from "@mui/material";
import {
  Camera,
  File,
  Image,
  LinkSimple,
  PaperPlaneTilt,
  Smiley,
  Sticker,
  User,
} from "phosphor-react";
import { useTheme, styled } from "@mui/material/styles";
import React, { useRef, useState } from "react";
import useResponsive from "../../hooks/useResponsive";
import data from "@emoji-mart/data";
import Picker from "@emoji-mart/react";
import { socket } from "../../socket";
import {useDispatch, useSelector } from "react-redux";
import { SendMessage,AddDirectMessage,SetLatestMessage } from "../../redux/slices/conversation";
import { set } from "date-fns";
import axios from "../../utils/axios";
import { UpdateBlockStatus } from "../../redux/slices/conversation";

const StyledInput = styled(TextField)(({ theme }) => ({
  "& .MuiInputBase-input": {
    paddingTop: "12px !important",
    paddingBottom: "12px !important",
  },
}));

const ChatInput = ({
  openPicker,
  setOpenPicker,
  setValue,
  value,
  inputRef,
}) => {
  return (
    <StyledInput
      inputRef={inputRef}
      value={value}
      onChange={(event) => {
        setValue(event.target.value);
      }}
      fullWidth
      placeholder="Write a message..."
      variant="filled"
      InputProps={{
        disableUnderline: true,
        startAdornment: (
          <Stack sx={{ width: "max-content" }}>
            <Stack
              sx={{
                position: "relative",
                display:"inline-block" 
              }}
            >
            </Stack>
          </Stack>
        ),
        endAdornment: (
          <Stack sx={{ position: "relative" }}>
            <InputAdornment>
              <IconButton
                onClick={() => {
                  setOpenPicker(!openPicker);
                }}
              >
                <Smiley />
              </IconButton>
            </InputAdornment>
          </Stack>
        ),
      }}
    />
  );
};

const Footer = () => {
  const theme = useTheme();
  const dispatch = useDispatch();
  const { conversationsList,currentConversationId } = useSelector(
    (state) => state.conversation
  );

  const currentConversation = conversationsList.find(
    (conv) => conv._id === currentConversationId
  );
  
  const user_id = window.localStorage.getItem("user_id");

  const isMobile = useResponsive("between", "md", "xs", "sm");

  const { sideBar } = useSelector((state) => state.app);

  const [openPicker, setOpenPicker] = React.useState(false);

  const [value, setValue] = useState("");
  const inputRef = useRef(null);

  function handleEmojiClick(emoji) {
    const input = inputRef.current;

    if (input) {
      const selectionStart = input.selectionStart;
      const selectionEnd = input.selectionEnd;

      setValue(
        value.substring(0, selectionStart) +
          emoji +
          value.substring(selectionEnd)
      );

      // Move the cursor to the end of the inserted emoji
      input.selectionStart = input.selectionEnd = selectionStart + 1;
    }
  }
  const handleBlockUser = async (status) => {
    try {
      const body = new URLSearchParams({
        convID: currentConversationId,
        status: status,
      });
      const response = await axios.post("/blockStatus", body);
      console.log("Block status updated successfully:", response.data);
      dispatch(UpdateBlockStatus(currentConversationId, status));
    } catch (error) {
      console.error("Error updating block status:", error);
    }
  };

  const isBlockedByCurrentUser = currentConversation.blockStatus === user_id;
  const isBlockedByOtherUser = currentConversation.blockStatus !== '' && currentConversation.blockStatus !== user_id;

  return (
    <Box
      sx={{
        position: "relative",
        backgroundColor: "transparent !important",
      }}
    >
      <Box
        p={isMobile ? 1 : 2}
        width={"100%"}
        sx={{
          backgroundColor:
            theme.palette.mode === "light"
              ? "#F8FAFF"
              : theme.palette.background,
          boxShadow: "0px 0px 2px rgba(0, 0, 0, 0.25)",
        }}
      >
        {isBlockedByCurrentUser ? (
          <Stack alignItems="center" spacing={2}>
            <Typography>Bạn đã chặn người dùng này và không thể gửi hay nhận tin nhắn đến họ</Typography>
            <Button
              variant="contained"
              color="secondary"
              onClick={() => handleBlockUser('')}
            >
              Bỏ chặn
            </Button>
          </Stack>
        ) : isBlockedByOtherUser ? (
          <Typography>Bạn đã bị người dùng này chặn và không thể gửi hay nhận tin nhắn đến họ</Typography>
        ) : (
        <Stack direction="row" alignItems={"center"} spacing={isMobile ? 1 : 3}>
          <Stack sx={{ width: "100%" }}>
            <Box
              style={{
                zIndex: 10,
                position: "fixed",
                display: openPicker ? "inline" : "none",
                bottom: 81,
                right: isMobile ? 20 : sideBar.open ? 420 : 100,
              }}
            >
              <Picker
                theme={theme.palette.mode}
                data={data}
                onEmojiSelect={(emoji) => {
                  handleEmojiClick(emoji.native);
                }}
              />
            </Box>
            {/* Chat Input */}
            <ChatInput
              inputRef={inputRef}
              value={value}
              setValue={setValue}
              openPicker={openPicker}
              setOpenPicker={setOpenPicker}
            />
          </Stack>
          <Box
            sx={{
              height: 48,
              width: 48,
              backgroundColor: theme.palette.primary.main,
              borderRadius: 1.5,
            }}
          >
            <Stack
              sx={{ height: "100%" }}
              alignItems={"center"}
              justifyContent="center"
            >
              <IconButton
                onClick={() => {
                  const receiver = conversationsList.find(conv => conv._id === currentConversationId).members[0]._id;
                  const message = {
                    conversation: currentConversationId,
                    sender: user_id,
                    receiver: receiver,
                    sentTime: (new Date()).toISOString(),
                    content: value,
                  };

                  socket.emit("sendMsg", message);
                  dispatch(AddDirectMessage({ message: message }));
                  dispatch(SetLatestMessage({ message: message }))
                  setValue("")
                  dispatch(SendMessage(message));
                }}
              >
                <PaperPlaneTilt color="#ffffff" />
              </IconButton>
            </Stack>
          </Box>
        </Stack>
        )}
      </Box>
    </Box>
  );
};

export default Footer;
